@import "../scss/core/functions/functions";
@import "../scss/core/settings/settings";              // settings
@import "../scss/core/mixins/mixins";
@import "../scss/core/variables/variables";
/* Let's highlight canvas boundaries */
#gjs {
    border: 3px solid #444;
}

/* Reset some default styling */
.gjs-cv-canvas {
    top: 0;
    width: 100%;
    height: 100%;
}
.gjs-block {
    width: auto;
    height: auto;
    min-height: auto;
}

.panel__top {
    padding: 0;
    width: 100%;
    display: flex;
    position: initial;
    justify-content: space-between;
    background-color: #444;
}
.panel__basic-actions {
    position: initial;
}

.editor-row {
    display: flex;
    justify-content: flex-start;
    align-items: stretch;
    flex-wrap: nowrap;
    //height: 300px;
}

.editor-canvas {
    flex-grow: 1;
}

.panel__right {
    flex-basis: 230px;
    position: relative;
    overflow-y: auto;
}
.panel__devices {
    position: initial;
    svg{
        width: 24px;
        height: 24px;
    }
}
.panel__switcher {
    position: initial;
}